import React from 'react';
import { Jumbotron, Container, Col, Row, Image } from 'react-bootstrap';
import Typist from 'react-typist';
import 'bootstrap/dist/css/bootstrap.min.css';

import me from '../img/me.jpg';

class Intro extends React.Component{
    componentDidMount(){
        console.log(this.props.bgc);
    }
    render(){
        const letterdelay = function(){
            return Math.random() * (900 - 400) + 400;
        }
        const cur = {
            show: true,
            blink: true,
            element: '/>',
            hideWhenDone: false,
            hideWhenDoneDelay: 1000,
        }
        return(
            <Jumbotron fluid style={{backgroundImage:this.props.bgc, color:'#ffffff'}}>
                <Container>
                    <Row>
                        <Col xs={12} md={8} lg={6}>
                            <center><Image src={me} width="300px" height="300px" style={{borderRadius:'50%'}}/></center>
                        </Col>
                        <Col xs={12} md={8} lg={6} style={{paddingTop:'25px'}}>
                            <h1 style={{ color:'#ffffff'}}>
                            <Typist cursor={cur}><Typist.Delay ms={letterdelay} />Hi<Typist.Delay ms={500} />, I‚Äôm <br/>&lt;P<Typist.Delay ms={letterdelay} />r<Typist.Delay ms={letterdelay} />a<Typist.Delay ms={letterdelay} />n<Typist.Delay ms={letterdelay} />a<Typist.Delay ms={letterdelay} />v<Typist.Delay ms={letterdelay} /> M<Typist.Delay ms={letterdelay} /> S</Typist><br/>
                            Student and Developer<br/>
                            </h1>
                            <p className="lead">
                                I am pursuing my engineering in Computer science at KSIT. I am a Fullstack developer.
                                My interests include, Machine Learning, Neural Networks and Deep Learning, Penetration Testing.
                                I love building applications and finding solutions which impact others lives. I love to participate 
                                in hackathons and meet new people.
                            </p>
                        </Col>
                    </Row>
                </Container>
            </Jumbotron>
        )
    }
}
export default Intro;